import React from 'react';
import 'bootstrap/dist/css/bootstrap.css';
import logo from './logo.svg';
import './App.css';
import Modal from 'react-modal';
 
const customStyles = {
  content : {
    top                   : '30%',
    left                  : '30%',
    right                 : '30%',
    bottom                : 'auto'
  }
};
 
Modal.setAppElement('#root1')
class MyComponent extends React.Component {
  constructor(props) {
    super(props);
    this.state = {
      error: null,
      isLoaded: false,
      items: [],
	  totalCount:0,
	  finalAmount:0
    };
	  this.calculateTotalCount = this.calculateTotalCount.bind(this);
  }
increaseCount(val){
	var index =this.state.items.indexOf(val);
	if(index>-1){
		if(this.state.items[index].count<10){
		this.state.items[index].count++;
		this.state.totalCount++;
			this.updateRates();
	
	this.setState({
		items:this.state.items
	});
		}else{
		alert("Max limit reached. Maximum cart limit is 10 for this item.");
	}
		}
	
}
	calculateTotalCount(){
		var totalCount=0;
		this.state.items.map(function(item){
			if(Number.parseInt(item.count,10)>0){
				totalCount = totalCount+ Number.parseInt(item.count,10);
			}
			});
		this.updateRates();
		this.setState({
			totalCount:totalCount
		});
	}
changeCount(val,key){
	if(document.getElementById(key).value<11){
	var index =this.state.items.indexOf(val);
	if(index>-1){
		this.state.items[index].count=document.getElementById(key).value;
	}
	this.calculateTotalCount();
	this.setState({
		items:this.state.items
	});
	}else{
		alert("Max limit reached. Maximum cart limit is 10 for this item.");
	}
}
	decreaseCount(val){
	var index =this.state.items.indexOf(val);
		if(index>-1){
			if(this.state.items[index].count>0){
				this.state.items[index].count--;
				this.state.totalCount--;
				this.updateRates();
			this.setState({
				items:this.state.items
			});
			}
		}
	}
	deleteCart(val){
	var index =this.state.items.indexOf(val);
		if(index>-1){
			if(this.state.items[index].count>0){
				this.state.items[index].count=0;
				this.calculateTotalCount();
				this.updateRates();
			this.setState({
				items:this.state.items
			});
			}
		}
	}
 updateRates() {
	 var finalAmount=0;
	  this.state.items.map(function(item){
			  if(item.count>0){
			  item.totalAmount = (item.count*item.discountPrice).toFixed(2);
				 finalAmount = finalAmount+Number.parseFloat(item.totalAmount);
			  }
			});
	 finalAmount = finalAmount.toFixed(2);
	 this.setState({
			finalAmount:finalAmount
		});
  }
  componentDidMount() {
    fetch("https://my-json-server.typicode.com/jubs16/Products/Products")
      .then(res => res.json())
      .then(
        (result) => {
			result.map(function(item){
				item.count=0;
				item.discount=Math.floor(Math.random() * 10)+1;
				item.discount=Number.parseInt(item.discount,10);
				item.discountPrice=item.price-(item.price*(item.discount/100));
			});
          this.setState({
            isLoaded: true,
            items: result
          });
        },
        (error) => {
          this.setState({
            isLoaded: true,
            error
          });
        }
      )
  }

  render() {
    const { error, isLoaded, items ,totalCount ,finalAmount } = this.state;
    if (error) {
      return <div>Error: {error.message}</div>;
    } else if (!isLoaded) {
      return <div>&nbsp;&nbsp;Loading...</div>;
    } else if(totalCount==0) {
      return (
		<div className="col-md-12 col-12 no-padding-lr pull-left">
		<div className=" col-md-8 col-12 no-padding-lr pull-left">
		<div className=" col-md-12 col-12 headings">
		  	<p>Casual Sneakers<span className="totalCounts">Cart({totalCount})</span></p>
		 </div>
		<div className="loadedItems col-md-12 col-12 pull-left">
        <ul>
          {items.map((item,key) => (
            <li key={item.name}>
			  <Products name={item.name} imgUrl={item.imgUrl} discountPrice={item.discountPrice} price={item.price} discount={item.discount} count={item.count} keyVal={key} increaseCounts={this.increaseCount.bind(this,item)} changeCounts={this.changeCount.bind(this,item,key)} decreaseCounts={this.decreaseCount.bind(this,item)} />
            </li>
          ))}
        </ul>
	  </div>
	  </div>
	  </div>
      );
    }else{
		return (
		<div className="col-md-12 col-12 no-padding-lr pull-left">
		<div className=" col-lg-8 col-md-12 col-sm-12 col-12 no-padding-lr pull-left">
		<div className=" col-md-12 col-12 headings">
		  	<p>Casual Sneakers<span className="totalCounts">Cart({totalCount})</span></p>
		 </div>
		<div className="loadedItems col-md-12 col-12 pull-left">
        <ul>
          {items.map((item,key) => (
            <li key={item.name}>
			  <Products name={item.name} imgUrl={item.imgUrl} discountPrice={item.discountPrice} price={item.price} discount={item.discount} count={item.count} keyVal={key} increaseCounts={this.increaseCount.bind(this,item)} changeCounts={this.changeCount.bind(this,item,key)} decreaseCounts={this.decreaseCount.bind(this,item)} />
            </li>
          ))}
        </ul>
	  </div>
	  </div>
		<div className="col-lg-4 col-sm-12 col-md-12 col-12 pull-left">
			<Cart count={totalCount} amount={finalAmount} deleteValue={this.deleteCart.bind(this)} allItems={items}/>
		</div>
		<a className="cartMobile" href="#cart"><img src="cart.svg"/><span className="no">{totalCount}</span></a>
	  </div>
      );
	}
  }
}
class Products extends React.Component {
  constructor(props) {
    super(props);
  }
  render() {
	  return(
		  <div className="col-md-12 col-12">
    <div className="col-lg-2 col-sm-4 col-md-4 col-4 removeleft">
				<img src={this.props.imgUrl}/>
			  </div>
              <div className="col-lg-3 col-sm-5 col-md-5 col-4 removeleft">
		  		<span>{this.props.name}</span>
                <span className="manufacture">Sold by : XYZ Manufacturers</span>
                <span className="manufacture green">-{this.props.discount}%off</span>
		  	  </div>
		  		<div className="col-lg-2  col-sm-3 col-md-3 col-3 no-padding-lr inputsNew removeleft">
		  		<button  onClick={this.props.increaseCounts}><span className="fas fa-chevron-up"></span></button>
                <span><input id={this.props.keyVal} value={this.props.count} onChange={this.props.changeCounts}/></span>
		  		<button  onClick={this.props.decreaseCounts} className="removeright"><span className="fas fa-chevron-down"></span></button >
		  	  </div>
		  <div className="col-lg-3  col-sm-9 col-md-9 col-8 priceList">
		  <p className="price"><span>M.R.P.:<span className="originalPrice">₹&nbsp;{this.props.price}</span></span></p>
		  <p className="discountedPrice">₹&nbsp;{this.props.discountPrice}&nbsp;x&nbsp;<input value={this.props.count} disabled="true"/></p>
		  </div>
		  <div className="col-lg-2 col-md-3 col-sm-3 col-4 no-padding-lr newOptions">
		  		<ul>
		  			<li><span title="Cash on delivery" className="fa fa-credit-card"></span></li>
		  			<li><span title="Replacement guarantee" className="fa fa-retweet"></span></li>
		  			<li><span title="Free delivery on purchase above ₹500" className="fa fa-bus"></span></li>
		  		</ul>
		  </div>
		  </div>
      );
    }
  }
class Cart extends React.Component {
  constructor(props) {
    super(props);
	  this.state = {
		  modalIsOpen: false
		};
	  this.openModal = this.openModal.bind(this);
	  this.afterOpenModal = this.afterOpenModal.bind(this);
	  this.closeModal = this.closeModal.bind(this);
	  this.alertModel = this.alertModel.bind(this);
  }openModal() {
		this.setState({modalIsOpen: true});
	  }

	  delete(val) {
		  this.props.deleteValue(val);
	  }
	  afterOpenModal() {
		// references are now sync'd and can be accessed.
		this.subtitle.style.color = '#f00';
		  document.getElementById("addChecked").checked=true;
	  }

	  closeModal() {
		this.setState({modalIsOpen: false});
	  }alertModel() {
		  /*if(document.getElementsByName("group2")[0].checked || document.getElementsByName("group2")[1].checked||document.getElementsByName("group2")[2].checked||document.getElementsByName("group2")[3].checked){*/
			  
		this.setState({modalIsOpen: false});
		  alert("Payment successfully completed");
		  window.location.reload();
		 /* }*/
	  }
  render() {
	  return(
		  <div className="col-md-12 col-12 cartBackground" id="cart">
		  <p className="cartheader">Total items({this.props.count})</p>
		  	<ul className="cartlist">
          {this.props.allItems.map((item,key) => (
					item.count?<li key={item.name}>
						{item.name}<a className="deletebtn" onClick={this.delete.bind(this,item)}><span className="fa fa-trash"></span></a><span>Quantity:<span>{item.count}</span>&nbsp;&nbsp;</span>
							
				  <div className="col-md-12 col-12 no-padding-lr priceList">
				  <p className="price fontSize12"><span>M.R.P.:<span className="originalPrice">₹&nbsp;{item.price}</span></span></p>
				  <p className="discountedPrice fontSize12">₹&nbsp;{item.discountPrice}&nbsp;x&nbsp;<input value={item.count} disabled="true"/><span className="finalAmt">₹&nbsp;{item.totalAmount}</span></p>
				  </div>
					</li>
		  
		  :""
          ))}
        </ul>
		<div className="col-md-12 col-12 text-center">
			<button className="btn btn-sm btn-danger"  onClick={this.openModal}>Checkout({this.props.amount})</button>
<Modal
          isOpen={this.state.modalIsOpen}
          onAfterOpen={this.afterOpenModal}
          onRequestClose={this.closeModal}
          style={customStyles}
          contentLabel="Checkout"
        >
 
          <h5 ref={subtitle => this.subtitle = subtitle} className="margin-bottom-15">Total Amount:₹&nbsp;{this.props.amount}</h5>
          <button className="modalclose btn btn-default btn-sm" onClick={this.closeModal}><span className="fa fa-times"></span></button>
          <div className="margin-bottom-10">Kindly select your mode of payment</div>
          <form>
            <fieldset id="group2">
				<label><input type="radio" value="value1" id="addChecked" name="group2"/>Cash on delivery</label>
				<label><input type="radio" value="value2" name="group2"/>Debit card</label>
				<label><input type="radio" value="value3" name="group2"/>Credit Card</label>
				<label><input type="radio" value="value4" name="group2"/>EMI options</label>
			  </fieldset>
            <button className="pull-right btn btn-danger btn-sm"  onClick={this.alertModel}>Proceed to pay</button>
          </form>
        </Modal>
			</div>
		  </div>
      );
    }
  }
export default MyComponent;